import {mapState} from 'vuex';
import many from '@/components/assets/userspic/many.vue'
import f from "@/application/functions";

export default {
  name: 'userspic',
  props: {
    users: Array,
    unseen: Number,
    slidesPerView: Number,
    chat: {}
  },

  data: function () {
    return {}
  },

  components: {
    many
  },

  created: () => {

  },

  computed: mapState({
    log: function () {
    },
    auth: state => state.auth,
    info: state => {

    },
    anotherUser: function () {

      var my = f.getmatrixid(this.core.mtrx.client.credentials.userId)
      var userInfo = {}
      this.chat.members.forEach(function (member) {
        if (member.id !== my) {
          return userInfo = member
        }
      })
      return userInfo
    }

  }),

  methods: {},
}
